<template>
<div id="settlement_record">
    <div class="tit"><span>提款账户</span> <span class="addtk" @click="modalbank">添加账户</span></div>
    <div class="store_bottom">
      <table>
        <thead>
          <tr>
          <th>姓名</th>
          <th>提款银行</th>
          <th>提款账号</th>
          <th>操作</th>
          </tr>
        </thead>
      <tbody data-v-9154b70e="">
          <td v-if="list.length == 0" colspan="11" align="center">
          <div class="none_now" >
          <img src="/static/icon/7.png">
          <div>暂无相关数据</div>
          </div>
          </td>
          <tr data-v-9154b70e="" v-for="(item,index) in list" :key="item.Id" v-if="list.length >= 1" :class="typeof item.div =='undefined' ? '' : 'chaoz'">
            <td data-v-9154b70e="" v-if="typeof item.div =='undefined'">{{item.name}}</td>
            <td data-v-9154b70e="" v-if="typeof item.div =='undefined'">{{item.bank_type}}</td>
            <td data-v-9154b70e="" v-if="typeof item.div =='undefined'">{{item.bank_card}}</td>
            <td data-v-9154b70e="" v-if="typeof item.div =='undefined' && tdindex -1 != index" style="color: rgb(56, 108, 250); cursor: pointer; font-weight: bold;" @click="tdchange(index)" >操作</td>
            <td data-v-9154b70e="" v-if="tdindex - 1 == index" style="color: rgb(56, 108, 250); cursor: pointer; font-weight: bold;" @click="tdclose()" >关闭</td>
            <td colspan="4" class="chaoz_box"  v-if="typeof item.div !='undefined'" >
              <div  @click.stop="delbank(item.data.Id)"><img src="/static/icon/14.png"><span>删除卡号</span></div>
            </td>
          </tr>
      </tbody>
      </table>
      <Page :total="page.total"  :page-size="15" :current="page.current" show-total class="fenye" v-if="list.length>=1" @on-change="ajaxlist($event)" />
    </div>
    <Spin fix v-if="spin"></Spin>
    <Modal v-model="modal1" width="520" footer-hide>
        <div slot="header" class="ivu-modal-header-inner">
            添加收款账户
        </div>
        <div id="receipt">
            <div class="in_box">
            <div class="hai_biaot"><span>银行类型</span></div>
            <div class="wb_box">
            <Select style="width:328px" placeholder="请选择银行类型" v-model="add_form.bank_type">
                <Option v-for="item in banktypearr" :value="item.value" :key="item.value">{{ item.value }}</Option>
            </Select>
            </div>
            <div class="hai_biaot"><span>开户姓名</span></div>
            <div class="wb_box"><Input  type="text"   v-model="add_form.name" placeholder="请输入银行卡姓名" style="width:328px" /></div>
            <div class="hai_biaot"><span>银行卡账号</span></div>
            <div class="wb_box"><Input  type="number" v-model="add_form.bank_card" placeholder="请输入银行卡账号" style="width:328px" /></div>
            </div>
        </div>
        <div data-v-25db8c90="" class="bottom_box">
        <span data-v-25db8c90="" @click="modal1 = false">取消</span>
        <span data-v-25db8c90="" @click="ajaxadd()">保存信息</span>
        </div>
        <Spin fix v-if="spin2"></Spin>
    </Modal>
    <Modal v-model="modal2" width="360">
        <p slot="header" style="color:#f60;text-align:center">
            <Icon type="ios-information-circle"></Icon>
            <span>是否删除该银行卡</span>
        </p>
        <div style="text-align:center">
            <p>删除该银行卡后,不可再次添加,如需恢复请联系客服</p>
            <p>是否继续?</p>
        </div>
        <div slot="footer">
            <Button type="error" size="large" long :loading="modal2_loading" @click="ajaxdel">确定</Button>
        </div>
    </Modal>
</div>
</template>
<style>
.ivu-select-placeholder{
  line-height:40px;
}
</style>
<script>
export default {
         data(){
            return {
               banktypearr: [
                    {
                        value:'支付宝'
                    },
                    {
                        value:'中国工商银行'
                    },
                    {
                        value:'中国邮政储蓄银行'
                    },
                    {
                        value:'中国农业银行'
                    },
                    {
                        value:'中国建设银行'
                    },
                    {
                        value:'中国招商银行'
                    },
                    {
                        value:'交通银行'
                    },
                    {
                        value:'民生银行'
                    },
                    {
                        value:'浦发银行'
                    },
                    {
                        value:'中信银行'
                    },
                    {
                        value:'中国银行'
                    },
                    {
                        value:'平安银行'
                    },
                    {
                        value:'其它银行'
                    }

                ],
               modal1:false,
               modal2:false,
               modal2_loading:false,
               spin:false,
               spin2:false,
               list:[],
               page:{
                 current:1,
                 total:''
               },
               add_form:'',
               tdindex:'',
               tdstatus:false,
               edit_form:''
            }
         },
         mounted(){
            this.ajaxlist(1);
         },
         methods: {
            modalbank(){
              this.modal1 = true;
              this.add_form = {code:100,name:'',bank_type:'',bank_card:''};
            },
            ajaxlist(page){
             this.spin = true;
             this.$http.post('api/user/settlement_account',{page:page}, res => {
                   this.spin = false;
                  if (res.status == 200) {
                      if(res.data.code == 200){
                       this.list = res.data.data.list;
                       this.page.current = res.data.data.current_page;
                       this.page.total = res.data.data.total;
                      }else if(res.data.code == 201){
                        this.$Message.warning(res.data.msg);
                      }
                  } else {
                     console.log(res.message);
                  }
             });
            },
            tdchange(i,e){
              if(this.tdstatus){
                this.list.splice(this.tdindex,1);
                var index = i >= this.tdindex ? i : i + 1;
              }else{
                this.tdstatus = true;
                var index = i + 1;
              }
              this.tdindex = index;
              var insert = {};
                  insert.data = JSON.parse(JSON.stringify(this.list[i]));
                  insert.div = true;
                  insert.Id = 'div';
              this.list.splice(index,0,insert);
            },
            tdclose(){
              if(this.tdindex != ""){
                this.list.splice(this.tdindex,1);
                this.tdstatus = false;
                this.tdindex = '';
              }
            },
            delbank(id){
              this.edit_form = {};
              this.edit_form.bank_id = id;
              this.modal2 = true;
            },
            ajaxdel(){
              this.modal2_loading = true;
              this.$http.post('api/user/settlement_account',{code:103,bank_id:this.edit_form.bank_id}, res => {
                    this.modal2_loading = false;
                   if (res.status == 200) {
                       if(res.data.code == 200){
                         this.$Message.success(res.data.msg)
                         this.modal2 = false;
                         this.tdclose();
                         this.ajaxlist(this.page.current);
                       }else if(res.data.code == 201){
                         this.$Message.warning(res.data.msg)
                       }
                   } else {
                      console.log(res.message);
                   }
              });
            },
            ajaxadd(){
              if(this.add_form.bank_type == ''){
                this.$Message.warning("请选择要添加的银行类型");
              }else if(this.add_form.bank_card.length < 10){
                this.$Message.warning("请填写正确的银行卡号");
              }else if(this.add_form.name == ''){
                this.$Message.warning("请填写姓名")
              }else{
                this.spin2 = true;
                this.$http.post('api/user/settlement_account',this.add_form, res => {
                      this.spin2 = false;
                     if (res.status == 200) {
                         if(res.data.code == 200){
                           this.$Message.success(res.data.msg)
                           this.modal1 = false;
                           this.ajaxlist(this.page.current);
                         }else if(res.data.code == 201){
                           this.$Message.warning(res.data.msg)
                         }
                     } else {
                        console.log(res.message);
                     }
                });
              }
            }
         }
    }
</script>



